import 'bootstrap/dist/css/bootstrap.css';


import './App.css';
import Button from './components/Button';
import Quote from './components/Quote';
import { useState } from 'react';
import Alert from './components/Alert';
import 'jquery/dist/jquery.min.js'
import 'bootstrap/dist/js/bootstrap.min.js'

function App() {

  const [quote, setQuote] = useState('Be messy and complicated and afraid and show up anyway', '');
  const [author, setAuthor] = useState('Glennon Doyle Melton', '');
  const [alertStatus, setAlertStatus] = useState (false);

  const generateCode  = async ()=>{
    const api =  "https://type.fit/api/quotes";
    const result = await fetch(api);
    const data = await result.json();
    console.log(data);
    if( data.length > 0){
    let index = Math.floor(Math.random()*data.length);
      setQuote(data[index].text)
      setAuthor(data[index].author || 'Anonymous')
  }
}
  
  const handleCopy =async ()=>{
    let content = document.getElementById('quoteText').textContent;
    navigator.clipboard.writeText(content);
    setAlertStatus(true);
    setTimeout(() => {
      setAlertStatus(false);
      console.log(content);
    }, 2000);
    // console.log(content);
    // alert("Copied the text: ");
  }


  return (
    <>
    <div className="container">
    {alertStatus && <Alert alertText="Copied"/>}
    <div className="Wrapper">
      <Quote text={quote} source={author}/>
      <Button handleOnclick={generateCode} handleCopy={handleCopy}/>

    </div>
    </div>
    </>
  );
}

export default App;
